import SortingView from '../view/sorting-view.js';//
import EventListView from '../view/event-list-view.js';//
import NoPointView from '../view/no-point-view.js';//
import PointPresenter from './point-presenter.js';
import { sort } from '../utils/utils-sort.js';
import { updateItem } from '../utils/utils.js';
import { sortType, enabledSortType } from '../const.js';
import { remove, render, replace } from '../framework/render.js';

export default class BoardPresenter {
  #container = null;
  #sortComponent = null;
  #eventListComponent = null;
  #pointModel = null;
  #offersModel = null;
  #destinationsModel = null;
  #points = [];
  #currentSortType = sortType.DAY;
  #sourcedBoardPoint = [];

  #pointPresenters = new Map();// 
  #noPointComponent = new NoPointView();

  constructor({container, pointModel: pointsModel, offersModel, destinationsModel}){
    this.#container = container;
    this.#pointModel = pointsModel;
    this.#offersModel = offersModel;
    this.#destinationsModel = destinationsModel;

    this.#points = sort[sortType.DAY]([...this.#pointModel.get()]);
  }

  init() {
    this.#renderBoard();
  }

  #renderPoint = (point) => {//   
    const pointPresenter = new PointPresenter({
      container:this.#eventListComponent.element,
      destinationsModel:this.#destinationsModel,
      offersModel:this.#offersModel,
      onDataChange:this.#pointChangeHandler,
      onModeChange:this.#modeChangeHandler,
    });
    pointPresenter.init(point);
    this.#pointPresenters.set(point.id, pointPresenter);
  };

  #sortPoints = (item) =>{
    this.#currentSortType = item;
    this.#points = sort[this.#currentSortType](this.#points);
  };

  #renderPoints = () => {//  
    this.#points.forEach((point)=>{
      this.#renderPoint(point);
    });
  };

  #clearPoints = () =>{// 
    this.#pointPresenters.forEach((presenter)=>presenter.destroy());
    this.#pointPresenters.clear();
  };

  #renderSort = () => {// 
    const prevSortComponent = this.#sortComponent;

    const sortTypes = Object.values(sortType)
      .map((type)=>({
        type,
        isChecked:(type === this.#currentSortType),
        isDisabled:!enabledSortType[type]
      }));
    this.#sortComponent = new SortingView({
      items:sortTypes,
      onItemChange:this.#sortTypeChangeHandler
    });
    if (prevSortComponent){
      replace(this.#sortComponent,prevSortComponent);
      remove(prevSortComponent);
    } else{
      render(this.#sortComponent,this.#container);
    }
  };

  #renderPointContainer = () => {
    this.#eventListComponent = new EventListView();
    render(this.#eventListComponent,this.#container);
  };

  #renderNoPoint = () => {//   
    render(this.#noPointComponent, this.#container);
  };

  #renderBoard = () => {
    if (this.#points.length === 0){
      render(this.#renderNoPoint, this.#container);//
      return;
    }
    this.#renderSort();
    this.#renderPointContainer();
    this.#renderPoints();
  };

  #pointChangeHandler = (updatedPoint) => {
    this.#points = updateItem(this.#points, updatedPoint);// 
    this.#sourcedBoardPoint = updateItem(this.#sourcedBoardPoint);
    this.#pointPresenters.get(updatedPoint.id).init(updatedPoint);
  };

  #sortTypeChangeHandler = (item)=>{
    this.#sortPoints(item);//
    this.#clearPoints();//
    this.#renderSort();// 
    this.#renderPoints();//  
  };

  #modeChangeHandler = () =>{//    
    this.#pointPresenters.forEach((presenter) => presenter.resetView());
  };

}

