import {render, replace, remove} from '../framework/render.js';
import CreateFormView from '../view/creation-form-view.js';// 
import WayPointView from '../view/waypoints-view.js';//  
import {Mode} from '../const.js';

export default class PointPresenter{
  #container = null;
  #pointComponent = null;
  #pointEditComponent = null;
  #offersModel = null;
  #destinationsModel = null;
  #point = null;
  #handleDataChange = null;
  #handleModeChange = null;
  #mode = Mode.DEFAULT;

  constructor({container, offersModel, destinationsModel, onDataChange, onModeChange}){
    this.#container = container;
    this.#offersModel = offersModel;
    this.#destinationsModel = destinationsModel;
    this.#handleDataChange = onDataChange;
    this.#handleModeChange = onModeChange;
  }

  init(point){
    this.#point = point;

    const prevPointComponent = this.#pointComponent;
    const prevPointEditComponent = this.#pointEditComponent;

    this.#pointComponent = new WayPointView({// 
      point:this.#point,
      pointDestination: this.#destinationsModel.getById(point.destination),
      pointOffers: this.#offersModel.getByType(point.type),
      onEditClick: this.#handleEditClick,//
      onFavoriteClick:this.#handlefavoriteClick,//
    });
    this.#pointEditComponent = new CreateFormView({// 
      point:this.#point,
      pointDestinations: this.#destinationsModel.get(),
      pointOffers: this.#offersModel.get(),
      onFormSubmit:this.#handleFormSubmit,//
      onArrowUpClick:this.#handleArrowUpClick,// 
      onDeleteClick:this.#handleDeleteClick,//
    });

    if(prevPointComponent === null || prevPointEditComponent === null){//  
      render(this.#pointComponent, this.#container);
      return;
    }
    if (this.#mode === Mode.DEFAULT){
      replace(this.#pointComponent, prevPointComponent);
    }
    if (this.#mode === Mode.EDITING){
      replace(this.#pointEditComponent,prevPointEditComponent);
    }
    remove(prevPointComponent);
    remove(prevPointEditComponent);
  }

  resetView = () => {//  
    if(this.#mode !== Mode.DEFAULT){
      this.#pointEditComponent.reset(this.#point);
      this.#replaceFormToPoint();
    }
  };

  destroy = () =>{//
    remove(this.#pointComponent);
    remove(this.#pointEditComponent);
  };

  #replacePointToForm = () => {
    replace(this.#pointEditComponent,this.#pointComponent);//   
    document.addEventListener('keydown',this.#escKeyDownHandler);
    this.#handleModeChange();
    this.#mode = Mode.EDITING;
  };

  #replaceFormToPoint = () => {
    replace(this.#pointComponent,this.#pointEditComponent);//    
    document.removeEventListener('keydown',this.#escKeyDownHandler);
    this.#mode = Mode.DEFAULT;
  };


  #escKeyDownHandler = (evt) => {
    if(evt.key === 'Escape'){//   
      evt.preventDefault();//   
      this.#pointEditComponent.reset(this.#point);
      this.#replaceFormToPoint();//    
    }
  };

  #handleArrowUpClick = () =>{
    this.#pointEditComponent.reset(this.#point);
    this.#replaceFormToPoint();//    
  };

  #handleEditClick = () => {//   
    this.#replacePointToForm();//   
  };

  #handleFormSubmit = (point) => {
    this.#replaceFormToPoint();//    
    this.#handleDataChange(point);
  };

  #handleDeleteClick = () => {
    document.removeEventListener('keydown', this.#escKeyDownHandler);
    remove(this.#pointEditComponent);
  };


  #handlefavoriteClick = () => {
    this.#handleDataChange({
      ...this.#point,
      isFavorite: !this.#point.isFavorite
    });
  };
}


